* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html, body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  width: 100%;
  font-family: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-family: var(--default-font), -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  overflow-x: hidden;
}

body{
  background-color: #FFFEFB;
  background-color: var(--white-color);
}

:root {
  --white-color: #FFFEFB;
  --black-color:  #1E1E1E;
  --green-color: #00B35B;
  --default-font: 'Poppins';
  --side-space: 5%;
}

/* poppins-regular - latin */
@font-face {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 400;
  src: url(/test-react/static/media/poppins-v20-latin-regular.9bb84f43.eot); /* IE9 Compat Modes */
  src: local(''),
       url(/test-react/static/media/poppins-v20-latin-regular.9bb84f43.eot?#iefix) format('embedded-opentype'), 
       url(/test-react/static/media/poppins-v20-latin-regular.9212f6f9.woff2) format('woff2'), 
       url(/test-react/static/media/poppins-v20-latin-regular.f8ed3280.woff) format('woff'), 
       url(/test-react/static/media/poppins-v20-latin-regular.891f6ba4.ttf) format('truetype'), 
       url(/test-react/static/media/poppins-v20-latin-regular.c26cc61a.svg#Poppins) format('svg'); /* Legacy iOS */
}

section {
  padding: 5%;
  padding: var(--side-space);
  min-height: 50vh;
}


h2{
  font-size: 2.5rem;
  line-height: 125%;
  padding: 1rem 0;
}

.top-section {
  position: relative;
  min-height: 100vh;
  display: flex;
  align-items: center;
  color: #FFFEFB;
  color: var(--white-color);
  text-align: center;
}
.top-section > img{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}
.page-intro h2 {
  padding: 3rem 0;
}
.page-intro p {
  line-height: 25px;
  word-spacing: 2px;
}

.white{
  background-color: #FFFEFB;
  background-color: var(--white-color);
}

.upper {
  text-transform: uppercase;
}

.summary-section {
  text-align: center;
  color: var(--black-color);
}

.summary-container {
  padding: 2rem 0;
}
.summary {
  width: 100%;
  padding: 2rem 0;
}
.summary .sum-num {
  font-size: 500%;
}
.summary .sum-img {
  width: 100%;
  height: 170px;
  margin-top: -80px;
}
.summary .sum-img img {
  height: 100%;
}
.summary .sum-topic {
  width: 100%;
  font-size: 120%;
}
.summary .sum-topic p:first-of-type {
  font-size: 85%;
}
.small-summary-box {
    background-color: var(--green-color);
    padding-bottom: 0;
    margin-bottom: var(--side-space);
}
  
.Box {
    width: 100%;
}

.images {
    display: flex;
    align-content: center;
    justify-content: center;
    align-items: center;
    margin: 5vh 0;
    height: 80vh;
    max-height: 90vw;
    position: relative;
}
.images img {
    position: absolute;
    max-width: 50vw;
}
.images img.left {
    -webkit-transform: translateX(-12vw);
            transform: translateX(-12vw);
    max-height: 65%;
}
.images img.right {
    -webkit-transform: translateX(12vw);
            transform: translateX(12vw);
    max-height: 100%;
}

.images .white {
    position: absolute;
    bottom: 0;
    width: 100vw;
    height: 50%;
}

.Box .info {
    color: var(--white-color);
    text-align: center;
}
.Box .info .title {
    font-size: 85%;
    padding: 2rem 0 1.5rem;
}
.Box .info .content {
    font-size: 120%;
    word-spacing: 2px;
    line-height: 2rem;
}
section.news .head {
    width: 100%;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    font-size: 300%;
    padding-bottom: 1rem;
}
section.news .head svg {
    height: 30px;
    width: 30px;
}
section.news {
    height: 100vh;
}
section.news > div {
    border-bottom: 1px solid var(--black-color);
}
section.news .news {
    width: 100%;
    display: -webkit-box;
    display: -moz-box;
    display: -ms-box;
    display: -o-box;
    overflow-x: auto;
}
section.news .news > div {
    width: 100%;
    margin-right: 3rem;
    padding: 2rem 0;
}
section.news .news > div:last-of-type{
    margin-right: 0;
}
section.news .news .image img {
    width: 100%;
    max-height: 300px;
    object-fit: cover;
}
section.news .news .info {
    font-size: 150%;
}
section.news .news .info .title {
    font-size: 60%;
    font-weight: bolder;
    padding: 0.5rem 0;
}
section.footer {
    background-color: var(--black-color);
    color: var(--white-color);
    font-size: 85%;
    font-weight: bold;
}
section.footer a{
    color: var(--white-color);
    text-decoration: none;
}

section.footer .links, section.footer .contacts, section.footer div form {
    font-size: 130%;
    font-weight: initial;
}


section.footer div div,  section.footer div form{
    display: flex;
    flex-direction: column;
}
section.footer div div {
    margin: 2rem 0;
}
section.footer div form > *{
    width: 100%;
    font-size: 110%;
    padding: .7rem 2rem;
    margin: .7rem 0;
    border: 1px solid var(--white-color);
    color: var(--white-color);
    background-color: var(--black-color);
    border-radius: 50px;
    outline: none;
}
